 *{
     font-family: Helvetica, Arial,  sans-serif;
 }
 body{
     padding: 60px;
 }

 input{
     color: grey;
     font-size: 1.2rem;
     width: 100%;
 }

 .card-grid{
     display: flex;
     flex-direction: row;
     flex-wrap: wrap;
     justify-content: center;
 }

 .card{
     width: 40%;
     align-content: center;
     border:1px solid grey;
     border-radius: 6px;
     overflow: hidden;
     margin-bottom: 10px;
     margin-right: 10px;
 }

 .card p{
     padding: 5px;
     text-align: center;
 }

 .card img{
     height: 10rem;
     width: 100%;
 }

 .h3-category{
     text-align: center;
 }